.contact{
    width: 100%;
    height: 90vh;
    display: flex;
}
.contact .leftSide{
    height: 100%;
    flex: 50%;
    background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSRN5vYSW0ChYYYQa8MJerQxHECs3njNxlfiA&usqp=CAU");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.contact .rightSide{
    height: 100%;
    flex: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.contact .rightSide h1{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
   text-align: center;
}
form{
    display: flex;
    flex-direction: column;
    width: auto;
    height: auto;
    padding: 30px;
}
form input{
    height: 40px;
    width: 80%;
    border: none;
    border-bottom: 1px solid black;
    color: black;
}
form textarea{
    margin-top: 15px;
    height: 70px;
    width: 80%;
    border: none;
    border-bottom: 1px solid black;
    color: black;
}
input::placeholder,
textarea::placeholder{
    font-weight: bold;
    color: black;
    font-family: Arial, Helvetica, sans-serif;
}
form input:focus,
textarea:focus{
    outline: none;
}
form label{
    margin-top: 15px;
    color: grey;
}
form button{
    margin-top: 40px;
    width: 50%;
    height: 10%;
    border: none;
    background-color: black;
    color: white;
    font-size: 20px;
    border-radius: 30px;
}
@media only screen and (max-width:750px){
    .contact{
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
    }
    form button{
        width: 70%;
        height: 10%;
        border: none;
        background-color: black;
        color: white;
        font-size: 20px;
        border-radius: 30px;
    }
}